function PageLink({ to, children, handleClick }) {

  let { keyboardProps } = useKeyboard({
    onKeyDown: (e) => handleKeyboard(e.key),
  });

  const handleKeyboard = (key) => {
    switch (key) {
      case 'Enter':
      case 'Space':
        onClick();
        break;
    }
  }

  const onClick = (e) => {
    if (e) e.stopPropagation();
    console.log(to);
    handleClick(e, to);
  }

  return (
    <>
      <ScrollLink  
        className={classNames(styles.pagebtn, styles.scrollLink)} 
        to={to}
        href={to}
        spy={true} 
        smooth={true} 
        duration={500}
      >
        {children}
      </ScrollLink>
      <Button 
        onClick={onClick} 
        className={styles.pagebtn} 
        focusClassName={styles.pagebtnFocus}
        disableDefaultClass
        {...keyboardProps}
      >
        {children}
      </Button>
    </>
  );
}

function Countdown() {
  const then = new Date('04/14/2021 8:00 PM');

  const timeUntil = () => {
    const now = new Date();
    const diff = then - now;

    const _second = 1000;
    const _minute = _second * 60;
    const _hour = _minute * 60;
    const _day = _hour * 24;

    const days = Math.floor(diff / _day);
    const hours = Math.floor((diff % _day) / _hour);
    const minutes = Math.floor((diff % _hour) / _minute);
    const seconds = Math.floor((diff % _minute) / _second);

    return { done: diff == 0, days: days, hrs: hours, min: minutes, sec: seconds };
  }

  const [counter, setCounter] = useState(timeUntil());

  useEffect(()=>{
    let timer = null;
    let time = timeUntil();
    if (time.done) clearInterval(timer);
    else {
      timer = setInterval(()=>{
        setCounter({...time});
      }, 1000);
    }
    
    return () => clearInterval(timer);
  }, [counter]);


  return (
    <>
      {counter &&
        <span>
          {counter.days} DAYS {counter.hrs} HRS {counter.min} MIN {counter.sec} SEC
        </span>
      } 
    </>
  );
}

