import React, { useState, useRef } from 'react'
import PropTypes from 'prop-types';
import { useFocusRing } from '@react-aria/focus';
import { useButton } from '@react-aria/button'
import { mergeProps } from '@react-aria/utils';
import { useKeyboard } from '@react-aria/interactions'
import { classdFn as classNames } from 'classd';
import { childrenOfType } from './utils/checkTypes';
import styles from '@styles/components/tabs.module.scss';

function Tabs(props) {
  const {
    defaultTabId,
    onSelectTab,
    selectedTabId: externalStateTabId,
    'aria-labelledBy': ariaLabelledBy,
    'aria-label': ariaLabel,
    className,
    tablistWrapperClassName,
    tabpanelWrapperClassName,
    children,
  } = props;

  // Create an object to model the tablist
  const tabs = React.Children.map(children, (child, index) => {
    // Build a unique ID for the tab
    // Used to associate a specific tab to its respective tabpanel
    let random = Math.random().toString(36).replace(/[^a-z]+/g, '').substr(2, 10);
    let ariaTabId = `tab-${child.props.id}-${random}`;
    
    // Create a tab object using the child data
    return {
      title: child.props.title, 
      id: `${child.props.id}`,
      panel: React.cloneElement(child, child.type === Panel ? {ariaTabId: ariaTabId} : null),
      ariaTabId: ariaTabId,
      ref: null,
    }
  });

  // Manage internal selected tab state
  // If state is externally controlled, an initial value is still set, 
  // but the external state gets used where relevant instead of this state
  const [selectedTabId, setSelectedTabId] = useState(
    defaultTabId 
      // If default prop exists, use it
      // Coerce to string for matching
      ? `${defaultTabId}`
      // Else use first tab in tablist as default
      : tabs[0].id
  );

  // Initialize keyed list of tab refs (used in managing focus)
  let tabRefs = {};
  // Callback passed to each Tab to lift up their ref
  const setRef = (ref, id) => {
    if (!tabRefs[id]) tabRefs[id] = ref;
  }

  // Manage keyboard interaction
  // Passed to the tablist element
  let { keyboardProps } = useKeyboard({
    onKeyDown: (e) => handleKeyboard(e.key),
  });

  // Handle keyboard input to set the active tab and focus it
  const handleKeyboard = (key) => {
    let index = tabs.findIndex(tab => tab.id === selectedTab.id);
    switch (key) {
      // Next tab
      case 'ArrowRight':
        if (index + 1 == tabs.length) {
          // If at end of tablist, loop to start
          handleSelectTab(tabs[0].id);
          tabRefs[tabs[0].id].current.focus();
        } else {
          // Increment tablist index
          handleSelectTab(tabs[++index].id);
          tabRefs[tabs[index].id].current.focus();
        }
        break;

      // Previous tab
      case 'ArrowLeft':
        if (index - 1 < 0) {
          // If at start of the tablist, loop to end 
          handleSelectTab(tabs[tabs.length - 1].id);
          tabRefs[tabs[tabs.length - 1].id].current.focus();
        } else {
          // Decrement tablist index
          handleSelectTab(tabs[--index].id);
          tabRefs[tabs[index].id].current.focus();
        }
        break;

      // First tab
      case 'Home':
        handleSelectTab(tabs[0].id);
        tabRefs[tabs[0].id].current.focus();
        break;

      // Last tab
      case 'End':
        handleSelectTab(tabs[tabs.length - 1].id);
        tabRefs[tabs[tabs.length - 1].id].current.focus();
        break;
    }
  }

  // Select a tab in the tablist, making it 'active'
  const handleSelectTab = (id) => {
    // Tab selection callback
    if (onSelectTab) onSelectTab(id);
    // If we're managing state internally, update it
    if (!externalStateTabId) setSelectedTabId(id);
  }

  // Define aria label props for the tablist element
  const ariaLabelProps = 
    ariaLabel 
      // If aria-label is defined, use it
      ? {'aria-label': ariaLabel} 
      // Else use aria-labelledby
      : ariaLabelledBy 
        ? {'aria-labelledby': ariaLabelledBy} 
        // If neither, don't render aria label props 
        : null;

  // Create an object to model the selected tab in the tablist
  // Helps render the right tab panel and 'active' styling on the right tab  
  const selectedTab = tabs.find((tab) => {
    if (externalStateTabId && `${externalStateTabId}` === tab.id) return true;
    else if (!externalStateTabId && selectedTabId === tab.id) return true;
    else return false;
  });

  return (
    <>
      <div className={tablistWrapperClassName}>
        <div
          className={classNames(className, styles.tablist)}
          role="tablist"
          {...ariaLabelProps}
          {...keyboardProps}
        >
          {tabs.map(tab =>
            <Tab
              key={tab.id}
              role="tab"
              active={selectedTab.id === tab.id}
              ariaTabId={tab.ariaTabId}
              setRef={{callback: setRef, id: tab.id}}
              onPress={()=>{handleSelectTab(tab.id)}}
            >
              {tab.title}
            </Tab>
          )}
        </div>
      </div>
      <div className={classNames(styles.panelWrapper, tabpanelWrapperClassName)}>
        {tabs.map(tab =>
          <div className={selectedTab.id === tab.id ? styles.activePanel : null} key={tab.id}>
            {tab.panel}
          </div> 
        )}
      </div>
    </>
  );
}

/**
 * Render a tabpanel element for a respective tab
 */
function Panel(props) {
  const {
    /* eslint-disable no-unused-vars */
    // These are required props that are used by the parent Tabs component
    title,
    id,
    /* eslint-enable no-unused-vars */
    ariaTabId,
    className,
    children,
  } = props;

  return (
    <div 
      className={classNames(styles.panel, className)}
      role="tabpanel"
      id={`${ariaTabId}-panel`}
      aria-labelledby={ariaTabId}
    >
      {children}
    </div>
  );
}

/**
 * Render a tab element for the tablist
 */
function Tab(props) {
  const {
    active, 
    ariaTabId,
    setRef,
    children,
  } = props;

  let ref = useRef();

  // Initialize button (tab trigger) props and focus state helpers
  let { buttonProps } = useButton(props, ref);
  let { isFocusVisible, focusProps } = useFocusRing();

  // Lift up ref to parent Tabs component
  setRef.callback(ref, setRef.id);
  
  // Create class list for current tab states
  const classes = classNames(
    styles.tab, 
    isFocusVisible ? styles.tabFocused : null,
    active ? styles.tabActive : null
  );

  // Only the active tab should be tab-key-able within a tablist
  const tabIndexProps = active ? { tabIndex: '0' } : { tabIndex: '-1' };

  return (
    <button 
      {...mergeProps(buttonProps, focusProps)}
      {...tabIndexProps}
      ref={ref}
      className={classes}
      role="tab" 
      id={ariaTabId}
      aria-selected={active ? true : false}
      aria-controls={`${ariaTabId}-panel`}
    >
      {children}
    </button>
  );
}

Tabs.propTypes = {
  ['aria-label']: (props, propName) => {
    if (!props[propName] && !props['aria-labelledby']) {
      return new Error(
        `Tabs: 'aria-label' or 'aria-labelledby' prop required in order to meet WIA ARIA spec: 3.22 Tabs. Read more: https://www.w3.org/TR/wai-aria-practices/#tabpanel`
      );
    }
  },
  children: childrenOfType(Panel),
}

Panel.propTypes = {
  id: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]).isRequired,
  title: PropTypes.any.isRequired,
}

Tabs.Panel = Panel;
export default Tabs;